"use client"
import {Input} from "@nextui-org/input";
import {Roboto} from "next/font/google"
import { useEffect, useState } from "react";
import { Button } from "@nextui-org/button";
import { BoxStudent } from "@/components/boxStudent/page"
import api from "@/services/api";

const roboto = Roboto({
  weight: "700",
  style: "normal"

})

type StudentType = {
  name: string,
  
  cpf:string,

  profission: string,

  course: string,

  registration: string,

  logradouro: string,

  numberHouse: number,

  complement: string,

  neighborhood: string,

  state: string,

  cep: number,

  maritalState: string,

  motherName: string,

  telephone: number,

  dateBirth: string,

  rg: string,
  
  email: string,
 
  startCourse: Date,

  financialSituation: string,
}

export default function Home() {

  const [students, setStudents] = useState([])

  const searchStudent = async (event: React.ChangeEvent<HTMLInputElement>) => {
    const student = await api.get(`student?search=${event.target.value}`, )
    
    setStudents(student.data)
  };

  const registerStudent = () => {

  }

  async function getAllStudents() {
    const students = await api.get('students')
    setStudents(students.data)
  }

  useEffect(() => {
    getAllStudents()
}, [])

  return (
    <main className={roboto.className}>
      <div className="pl-5 pr-5">
        <h1 className="text-[#329cca] text-3xl mt-10">Gestão de Alunos</h1>
        <p className="mt-2 text-[#949494] text-base">Configure e cadastre Alunos</p>

        <section className="flex items-center justify-between">
          <div className="mt-7">
            <h3 className="text-[#727272] mb-2">Alunos</h3>
            <Input 
              isClearable
              onChange={(e) => searchStudent(e)}
              radius="lg"
              color="default"
              placeholder="busque por nome ou cpf"
              />
          </div>
          <Button 
          color="primary"
          onClick={() => registerStudent()}
          >
            Cadastrar aluno
          </Button>
        </section>

        <section className="mt-8 w-full">
          <BoxStudent students={students} />
        </section>
      </div>
    </main>
  );
}
