import { Button } from "@nextui-org/button";
import {
    Modal, 
    ModalContent, 
    ModalHeader, 
    ModalBody, 
    ModalFooter,
    useDisclosure
    } from "@nextui-org/modal";

    interface modalDeleteProps {
        children?: any
        description?: string
        studentName: string
        onSubmit: () => void
     }


export function ModalEdit({ onSubmit, children, studentName,  }: modalDeleteProps) {

    return (
        <ModalContent>
        {(onClose) => (
            <>
            <ModalHeader className="flex flex-col gap-1 bg-[#03A9F4]">Aluno {'>'} {studentName} </ModalHeader>
            <ModalBody>
                <p>id: {studentName}</p>
                {children}
                
            </ModalBody>
            <ModalFooter className="pr-10 pb-5">
               
                <Button color="danger"  variant="light" onPress={onClose}>
                    Cancelar
                </Button>

                <Button className="text-white w-28 h-15 text-lg bg-[#03A9F4]"  variant="solid" onClick={onSubmit}  onPress={onClose}>
                   Salvar
                </Button>
            </ModalFooter>
            </>
        )}
        </ModalContent>
    

    )
}